import React, { Component } from 'react';
import ContainerLayout from '../components/containerLayout';
import VideoLoder from '../components/videoLoader';
import importCharts from '../utils/importCharts'
import ChartList from '../components/chartList';

class Charts extends Component {

    constructor(props) {
        super(props);
        this.state = {
            charts: []
        }
    }

    componentDidMount() {
        importCharts((chart) => {
            this.setState((state) => { return { charts: state.charts.concat([chart]) } })
        });
    }

    render() {
        const { charts } = this.state;
        return (
            <ContainerLayout>
                <h1>Charts</h1>
                <ChartList charts={charts} />
            </ContainerLayout>
        )
    }
};

export default Charts;